import { Layout, Menu } from 'antd';
import { Link } from 'react-router-dom';
import { UnorderedListOutlined, UserOutlined, SettingOutlined } from '@ant-design/icons';
import React from 'react';

const { Header, Content, Footer } = Layout;

const nav_items = (props) => [
  { label: <Link to={`/`}>Статьи</Link>, key: 'articles', icon: <UnorderedListOutlined /> },
  { label: <Link to={`/users`}>Пользователи</Link>, key: 'users', icon: <UserOutlined /> },
  { label: <Link to={`/settings`}>Настройки</Link>, key: 'settings', icon: <SettingOutlined /> }
]



class MainMenu extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
    }
    
  }

  render() {
    let selected = []
    selected = ['payments']

    return (
      <Layout>
        <Header
          style={{
            position: 'fixed',
            zIndex: 2,
            width: '100%',
            background: '#fff'
          }}
        >

          <Menu
            theme="light"
            mode="horizontal"
            defaultSelectedKeys={selected}
            items={nav_items(this.props)}
            style={{ float: 'left' }}
          />

        </Header>

        <Content
          className="site-layout"
          style={{
            padding: '10px 20px',
            marginTop: 64,
          }}
        >

          <div
            className="site-layout-background"
            style={{
              padding: 15,
              minHeight: "83.5vh",
            }}
          >
            {this.props.content}
          </div>

        </Content>

        <Footer
          style={{
            textAlign: 'center',
          }}
        >
          2022 Made by @SFFSky
        </Footer>
      </Layout>
    )
  };

}

export default MainMenu;