import { Button, Modal, Checkbox, Col, InputNumber, Radio, Rate, Row, Select, Slider, Switch, Upload, Input, Space, Form, Popover, Card, Avatar } from 'antd';

import React, { useState } from 'react';
import { EditOutlined, InboxOutlined, UploadOutlined } from '@ant-design/icons';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import axios from 'axios';

const { Option } = Select;
const { Meta } = Card;

const normFile = (e) => {
    console.log('Upload event:', e);

    if (Array.isArray(e)) {
        return e;
    }
    // return e?.fileList;
};

const tailLayout = {
    wrapperCol: {
        offset: 18,
        span: 15,
    },
};

const formItemLayout = {
    labelCol: {
        span: 4,
    },
    wrapperCol: {
        span: 19,
    },
};

const getBase64 = (file) =>
    new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = (error) => reject(error);
    });

const NewArticle = (props) => {

    const ru_roles = {
        ADMIN: "Администратор",
        MANAGER: "Менеджер",
        COPYWRITER: "Копирайтер"
    }

    const [isModalOpen, setIsModalOpen] = useState(false);
    const [value, setValue] = useState('');

    const [tokenT, setTokenT] = useState('');
    const [TableCRMUsers, setTableCRMUsers] = useState('');

    const [articleLink, setArticleLink] = useState('');
    const [main_pic, setMainPic] = useState(null);
    const [header_pic, setHeaderPic] = useState(null);

    const [previewOpen, setPreviewOpen] = useState(false);
    const [previewImage, setPreviewImage] = useState('');
    const [previewTitle, setPreviewTitle] = useState('')

    const [previewOpen2, setPreviewOpen2] = useState(false);
    const [previewImage2, setPreviewImage2] = useState('');
    const [previewTitle2, setPreviewTitle2] = useState('')

    const onFinish = (values) => {
        const formData = new FormData();
        // formData.append("project_tablecrm_id", null);
        // formData.append("client_tablecrm", null);
        formData.append("isPublish", values.isPublish);
        // formData.append("seo_url", null);
        // formData.append("performer", null);
        formData.append("first_sentence", values.first_sentence);
        formData.append("isPublic", values.isPublic);
        // formData.append("project_tablecrm", null);
        // formData.append("client_tablecrm_id", null);
        formData.append("title", values.title);
        formData.append("content", value);
        formData.append("price_hour", parseFloat(values.price_hour));
        formData.append("content", values.content);
        formData.append("tags", []);
        formData.append("category", []);

        if (header_pic) {
            formData.append("header_image", header_pic);
        }

        if (main_pic) {
            formData.append("main_image", main_pic);
        }

        console.log(values)

        const resp = axios.patch(articleLink, formData, {
            headers: {
                // "content-type": "multipart/form-data",
                "token": props.token,
            },

        }).then(response => {
            setIsModalOpen(false)
        });
    };

    const showModal = () => {
        const formData = new FormData();
        formData.append("owner", props.user.id);
        axios.post("https://promo.productlab.pro/api/article", formData, {
            headers: {
                // "content-type": "multipart/form-data",
                "token": props.token,
            },

        }).then(response => {
            setArticleLink(`https://promo.productlab.pro/api/article/${response.data.id}`)
            setIsModalOpen(true);

            // axios.get("https://promo.productlab.pro/api/token_tablecrm").then(response => {
            //     setTokenT(response.data);
            //     console.log
            //     axios.get(`https://tablecrm.com/api/v1/cashbox_users?token=${tokenT}`).then(response => {
            //         setTableCRMUsers(response.data);
            //         setIsModalOpen(true);
            //     })
            // })
        });
    };
    const handleOk = () => {
        setIsModalOpen(false);
    };
    const handleCancel = () => {
        setIsModalOpen(false);
    };

    const handleCancelPr = () => setPreviewOpen(false);
    const handleCancelPr2 = () => setPreviewOpen(false);

    const dummyRequest = ({ file, onSuccess }) => {
        setTimeout(() => {
            onSuccess("ok");
            setMainPic(file);
        }, 0);
    };

    const dummyRequest2 = ({ file, onSuccess }) => {
        setTimeout(() => {
            onSuccess("ok");
            setHeaderPic(file);
        }, 0);
    };

    const handlePreview = async (file) => {
        if (!file.url && !file.preview) {
            file.preview = await getBase64(file.originFileObj);
        }
        setPreviewImage(file.url || file.preview);
        setPreviewOpen(true);
        setPreviewTitle("Основная картинка");
    };

    const handlePreview2 = async (file) => {
        if (!file.url && !file.preview) {
            file.preview = await getBase64(file.originFileObj);
        }
        setPreviewImage(file.url || file.preview);
        setPreviewOpen(true);
        setPreviewTitle("Главная картинка в шапке страницы");
    };


    return (
        <>
            <Button type="default" onClick={showModal} style={{ marginBottom: 15, marginTop: -10 }}>Новая статья</Button>
            <Modal title="Создание статьи" width={"65%"} open={isModalOpen} onOk={handleOk} onCancel={handleCancel} footer={null}>

                <Form
                    name="validate_other"
                    {...formItemLayout}
                    onFinish={onFinish}
                    initialValues={{
                        isPublic: true,
                        isPublish: true,
                        price_hour: 1
                    }}
                >

                    <Form.Item
                        label="Ссылка на статью"
                    >
                        <Space direction='horizontal'>

                            <Input style={{ width: 750 }} value={articleLink} />

                            <Popover content="Ссылка скопирована в буфер обмена" trigger="click">
                                <Button onClick={() => navigator.clipboard.writeText(articleLink)} >Скопировать</Button>
                            </Popover>
                        </Space>
                    </Form.Item>

                    <Form.Item
                        label="Статью написал"
                    >
                        <Meta
                            avatar={<Avatar src={`https://promo.productlab.pro/api/${props.user.profile_pic}`} />}
                            title={<>{props.user.name} - <b>{ru_roles[props.user.role]}</b></>}
                        />
                    </Form.Item>

                    <Form.Item
                        name="title"
                        label="Название статьи"
                        rules={[
                            {
                                required: true,
                                message: 'Название статьи обязательно!',
                            },
                        ]}>
                        <Input placeholder='Введите название статьи' />
                    </Form.Item>
                    <Form.Item
                        name="first_sentence"
                        label="Первое предложение"
                        hasFeedback
                        rules={[
                            {
                                required: true,
                                message: 'Первое предложение статьи обязательно!',
                            },
                        ]}
                    >
                        <Input placeholder='Введите первое предложение статьи' />
                    </Form.Item>

                    <Form.Item
                        name="content"
                        label="Контент"
                        hasFeedback
                    // rules={[
                    //     {
                    //         required: true,
                    //         message: 'Первое предложение статьи обязательно!',
                    //     },
                    // ]}
                    >
                        <ReactQuill
                            theme="snow"
                            value={value}
                            onChange={setValue}
                        />
                    </Form.Item>

                    <Form.Item
                        name="isPublic"
                        label="Тип статьи"
                        rules={[
                            {
                                required: true,
                                message: 'Please pick an item!',
                            },
                        ]}
                    >
                        <Radio.Group>
                            <Radio.Button value={true}>Публичная</Radio.Button>
                            <Radio.Button value={false}>Непубличная</Radio.Button>
                        </Radio.Group>
                    </Form.Item>

                    <Form.Item
                        name="isPublish"
                        label="Статус"
                        rules={[
                            {
                                required: true,
                                message: 'Please pick an item!',
                            },
                        ]}
                    >
                        <Radio.Group>
                            <Radio.Button value={true}>Опубликована</Radio.Button>
                            <Radio.Button value={false}>Не опубликована</Radio.Button>
                        </Radio.Group>
                    </Form.Item>

                    <Form.Item label="Основная картинка">
                        <Form.Item getValueFromEvent={normFile} noStyle >
                            <Upload onPreview={handlePreview} listType="picture-card" name="files" customRequest={dummyRequest} accept=".jpg, .jpeg, .png" maxCount={1}>
                                Загрузить...
                            </Upload>
                            <Modal open={previewOpen} title={previewTitle} footer={null} onCancel={handleCancelPr}>
                                <img
                                    alt="example"
                                    style={{
                                        width: '100%',
                                    }}
                                    src={previewImage}
                                />
                            </Modal>
                        </Form.Item>
                    </Form.Item>

                    <Form.Item label="Картинка в шапке страницы">
                        <Form.Item getValueFromEvent={normFile} noStyle >
                            <Upload onPreview={handlePreview2} listType="picture-card" name="files" customRequest={dummyRequest2} accept=".jpg, .jpeg, .png" maxCount={1}>
                                Загрузить...
                            </Upload>
                            <Modal open={previewOpen2} title={previewTitle2} footer={null} onCancel={handleCancelPr2}>
                                <img
                                    alt="example"
                                    style={{
                                        width: '100%',
                                    }}
                                    src={previewImage2}
                                />
                            </Modal>
                        </Form.Item>
                    </Form.Item>

                    <Form.Item label="Стоимость реализации проекта">
                        <Form.Item name="price_hour" noStyle>
                            <InputNumber style={{ marginLeft: 15 }} min={1} />
                        </Form.Item>
                        <span className="ant-form-text"> час(ов)</span>
                    </Form.Item>



                    <Form.Item {...tailLayout}>
                        <Button type="primary" htmlType="submit" style={{ marginRight: 5 }}>
                            Подтвердить
                        </Button>
                        <Button htmlType="button" onClick={handleCancel}>
                            Отмена
                        </Button>
                    </Form.Item>
                </Form>


            </Modal>
        </>
    );
};
export default NewArticle;