import { Card, Col, Row, Avatar, Popover, Pagination } from 'antd';
import { EditOutlined, EllipsisOutlined, SettingOutlined, LinkOutlined } from '@ant-design/icons';
import React from 'react';
import axios from 'axios';
import CopyLink from './ClipBoard';
import EditArticle from './EditArticle';

import no_image from './images/no_main.png'
import NewArticle from './NewArticle';

const { Meta } = Card;

class ArticleCard extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            loaded: false
        }
    }

    fetch = (page, url = {}) => {
        const limit = 18
        const offset = (page * 18) - 18

        axios.get(url, { params: { limit: limit, offset: offset } })
            .then(response => {
                let cards = []
                for (var i = 0; i < response.data.result.length; i++) {
                    let profile_pic = "https://joeschmoe.io/api/v1/random"
                    let owner = "Не указан"
                    let owner_role = ""
                    let image_src = no_image
                    let buttons = [<CopyLink link={`https://promo.productlab.pro/api/articles/${response.data.result[i].id}`} />]
                    console.log(this.props)
                    if (response.data.result[i].owner && response.data.result[i].owner.profile_pic) {
                        profile_pic = `https://promo.productlab.pro/api/${response.data.result[i].owner.profile_pic}`
                    }

                    if (response.data.result[i].main_pic) {
                        image_src = `https://promo.productlab.pro/api/${response.data.result[i].main_pic}`
                    }

                    if (response.data.result[i].owner) {
                        owner = response.data.result[i].owner.name
                        if (response.data.result[i].owner.role === "ADMIN") { owner_role = "Администратор" }
                        if (response.data.result[i].owner.role === "MANAGER") { owner_role = "Менеджер" }
                        if (response.data.result[i].owner.role === "COPYWRITER") { owner_role = "Копирайтер" }
                    }

                    if (this.props.user) {
                        if (this.props.user.role === "MANAGER") {
                            if (response.data.result[i].owner.id === this.props.user.id) {
                                buttons.unshift(<EditArticle user={this.props.user} article={response.data.result[i]} token={this.props.token} />)
                            }
                        }
                        if (this.props.user.role === "ADMIN") {
                            buttons.unshift(<EditArticle user={this.props.user} article={response.data.result[i]} token={this.props.token} />)
                        }
                        if (this.props.user.role === "COPYWRITER") {
                            buttons.unshift(<EditArticle user={this.props.user} article={response.data.result[i]} token={this.props.token} />)
                        }
                    }

                    console.log(response.data)

                    cards.push(
                        // <Col key={response.data[i].id} span={4}>
                        <Card
                            key={response.data.result[i].id}
                            style={{
                                width: 300,
                                marginBottom: 15,
                                marginLeft: 5
                            }}
                            cover={
                                <img
                                    style={{ width: 300, height: 300 }}
                                    alt="image"
                                    src={image_src}
                                />
                            }
                            actions={buttons}
                        >
                            <Popover content={owner_role} title={owner}>
                                <Meta
                                    avatar={<Avatar src={profile_pic} />}
                                    title={response.data.result[i].title ? response.data.result[i].title : "Нет названия"}
                                    description={`${response.data.result[i].first_sentence ? response.data.result[i].first_sentence : "Нет описания"}\n`}
                                />
                                <br />
                                {response.data.result[i].isPublic ? "Статья публичная" : "Статья приватная"}
                                <br />
                                {response.data.result[i].isPublish ? "Статья обупликована" : "Статья не обупликована"}
                            </Popover>
                        </Card>
                        // </Col>
                    )
                }
                this.setState({ Cards: cards, CardsC: response.data.count, loaded: true })
            });
    }

    componentDidMount() {
        this.fetch(1, 'https://promo.productlab.pro/api/article')
    }


    render() {
        return (
            this.state.loaded ? <div >

                {this.props.user ? <NewArticle user={this.props.user} token={this.props.token} /> : null}

                <Row >
                    {this.state.Cards}
                </Row>
                <Pagination
                    onChange={(page) => { this.fetch(page, 'https://promo.productlab.pro/api/article') }}
                    pageSize={18}
                    total={this.state.CardsC}
                    style={{ float: "right" }}
                    showTotal={(total, range) => `Показаны ${range[0]}-${range[1]} из ${total} статей`}
                />
            </div > : null
        )
    }
}
export default ArticleCard;