import './App.css'
import React from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";

import MainMenu from './Menu';
import ArticleCard from './Card';

import { UnorderedListOutlined, UserOutlined, SettingOutlined } from '@ant-design/icons';

import { Button, Space, Layout, Menu } from 'antd';

import axios from 'axios';
import Users from './Users';
import Settings from './Settings';
import NewArticle from './NewArticle';

const { Header, Content, Footer } = Layout;

const urlSearchParams = new URLSearchParams(window.location.search);
const params = Object.fromEntries(urlSearchParams.entries());



class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      loaded: false,
      routes:
        <Routes>
          <Route
            path="/"
            element={<ArticleCard />}
          />
        </Routes>,

      nav_items: [{ label: <Link to={`/`}>Статьи</Link>, key: 'articles', icon: <UnorderedListOutlined /> }]
    }

  }

  componentDidMount() {

    if (params.token) {
      axios.get(`https://promo.productlab.pro/api/user/${params.token}`)
        .then(response => {
          if (response.data.role === "ADMIN") {
            this.setState({
              loaded: true,
              routes:
                <Routes>
                  <Route
                    path="/"
                    element={<ArticleCard user={response.data} token={params.token} />}
                  />

                  <Route
                    path="/users"
                    element={<Users token={params.token} />}
                  />

                  <Route
                    path="/settings"
                    element={<Settings token={params.token} />}
                  />

                </Routes>,
              nav_items: [
                { label: <Link to={`/?token=${params.token}`}>Статьи</Link>, key: 'articles', icon: <UnorderedListOutlined /> },
                { label: <Link to={`/users?token=${params.token}`}>Пользователи</Link>, key: 'users', icon: <UserOutlined /> },
                { label: <Link to={`/settings?token=${params.token}`}>Настройки</Link>, key: 'settings', icon: <SettingOutlined /> }
              ]
            })
          }
          if (response.data.role === "MANAGER") {
            this.setState({
              loaded: true,
              routes:
                <Routes>
                  <Route
                    path="/"
                    element={<ArticleCard user={response.data} token={params.token} />}
                  />
                </Routes>,
              nav_items: [
                { label: <Link to={`/?token=${params.token}`}>Статьи</Link>, key: 'articles', icon: <UnorderedListOutlined /> },
              ]
            })
          }
          if (response.data.role === "COPYWRITER") {
            this.setState({
              loaded: true,
              routes:
                <Routes>
                  <Route
                    path="/"
                    element={<ArticleCard user={response.data} token={params.token} />}
                  />
                </Routes>,
              nav_items: [
                { label: <Link to={`/?token=${params.token}`}>Статьи</Link>, key: 'articles', icon: <UnorderedListOutlined /> },
              ]
            })
          }
        })
    }

    else {
      this.setState({ loaded: true })
    }

    // else {
    //   nav_items = (props) => [
    //     { label: <Link to={`/`}>Статьи</Link>, key: 'articles', icon: <UnorderedListOutlined /> },
    //     { label: <Link to={`/users`}>Пользователи</Link>, key: 'users', icon: <UserOutlined /> },
    //     { label: <Link to={`/settings`}>Настройки</Link>, key: 'settings', icon: <SettingOutlined /> }
    //   ]
    // }

  }

  render() {
    let selected = []
    selected = ['payments']

    console.log(this.state)

    return (
      <BrowserRouter>
        <Layout>
          <Header
            style={{
              position: 'fixed',
              zIndex: 2,
              width: '100%',
              background: '#fff'
            }}
          >

            <Menu
              theme="light"
              mode="horizontal"
              defaultSelectedKeys={selected}
              items={this.state.nav_items}
            // style={{ float: 'left' }}
            />

          </Header>

          <Content
            className="site-layout"
            style={{
              padding: '10px 20px',
              marginTop: 64,
            }}
          >

            <div
              className="site-layout-background"
              style={{
                padding: 15,
                minHeight: "83.5vh",
              }}
            >

              {/* <Routes>
                <Route
                  path="/"
                  element={<ArticleCard />}
                />

                <Route
                  path="/users"
                  element={<Users token={params.token} />}
                />

                <Route
                  path="/settings"
                  element={<Settings token={params.token} />}
                />

              </Routes> */}
              {this.state.loaded ? this.state.routes: null}
            </div>

          </Content>

          <Footer
            style={{
              textAlign: 'center',
            }}
          >
            2022 Made by @SFFSky
          </Footer>
        </Layout>
      </BrowserRouter >
    )
  }

}

export default App;
