import './App.css'
import React from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";

import MainMenu from './Menu';
import ArticleCard from './Card';

import { UnorderedListOutlined, UserOutlined, SettingOutlined } from '@ant-design/icons';

import { Button, Space, Layout, Menu, Input } from 'antd';

import axios from 'axios';
import Users from './Users';

const { Header, Content, Footer } = Layout;


class Settings extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            loaded1: false,
            loaded2: false
        }

    }

    componentDidMount() {

        axios({
            url: 'https://promo.productlab.pro/api/token_tablecrm',
            method: 'get',
            headers: {
                token: this.props.token
            },
        })
            .then(response => {
                this.setState({ startTA: response.data, loaded1: true })
            })


        axios({
            url: 'https://promo.productlab.pro/api/token_useragentcc',
            method: 'get',
            headers: {
                token: this.props.token
            },
        })
            .then(response => {
                this.setState({ startUA: response.data, loaded2: true })
            })

    }

    updateUA = () => {
        if (this.state.ua) {

            axios({
                url: 'https://promo.productlab.pro/api/token_useragentcc',
                method: 'put',
                headers: {
                    token: this.props.token
                },
                data: {
                    data: this.state.ua
                }
            })
                .then(response => {
                    console.log(response)
                })
        }
    }

    updateTA = () => {
        if (this.state.tablecrm) {

            axios({
                url: 'https://promo.productlab.pro/api/token_tablecrm',
                method: 'put',
                headers: {
                    token: this.props.token
                },
                data: {
                    data: this.state.tablecrm
                }
            })
                .then(response => {
                    console.log(response)
                })
        }
    }

    render() {

        return (
            this.state.loaded1 && this.state.loaded2 ? <div>
            <Space size={"large"} direction="horizontal">
                Токен user-agent.cc
                <Input defaultValue={this.state.startUA} onChange={(e) => this.setState({ ua: e.target.value })} style={{ width: 650 }} placeholder="Введите токен user-agent.cc" />
                <Button onClick={this.updateUA}>
                    Обновить токен
                </Button>
            </Space>
            <br />
            <br />
            <Space size={"large"} direction="horizontal">
                Токен tablecrm.com
                <Input defaultValue={this.state.startTA} onChange={(e) => this.setState({ tablecrm: e.target.value })} style={{ width: 650 }} placeholder="Введите токен tablecrm.com" />
                <Button onClick={this.updateTA}>
                    Обновить токен
                </Button>
            </Space>
        </div> : null
        )
    }

}

export default Settings;
