import { Card, Col, Row, Avatar, Popover, Pagination } from 'antd';
import { EditOutlined, EllipsisOutlined, SettingOutlined, LinkOutlined } from '@ant-design/icons';
import React from 'react';
import axios from 'axios';
import CopyLink from './ClipBoard';
import EditArticle from './EditArticle';
import no_ava from './images/no_ava.png'

const { Meta } = Card;

class Users extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            loaded: false
        }
    }

    fetch = (page, url = {}) => {
        const limit = 18
        const offset = (page * 18) - 18

        axios({
            url: url,
            method: 'get',
            params: {
                limit: limit,
                offset: offset,
            },
            headers: {
                'token': this.props.token
            },
        })
            .then(response => {
                let cards = []
                for (var i = 0; i < response.data.result.length; i++) {
                    let profile_pic = 'https://promo.productlab.pro/api/' + response.data.result[i].profile_pic
                    let role = response.data.result[i].role

                    let ru_role = ""

                    if (!response.data.result[i].profile_pic) {
                        profile_pic = no_ava
                    }

                    if (role === "ADMIN") { ru_role = "Администратор" }
                    if (role === "MANAGER") { ru_role = "Менеджер" }
                    if (role === "COPYWRITER") { ru_role = "Копирайтер" }

                    cards.push(
                        // <Col key={response.data[i].id} span={4}>
                        <Card
                            key={response.data.result[i].id}
                            style={{
                                width: 300,
                                marginBottom: 15,
                                marginLeft: 5
                            }}
                            cover={
                                <img
                                    alt="image"
                                    src={profile_pic}
                                />
                            }
                        >

                            <Meta
                                // avatar={<Avatar src={profile_pic} />}
                                title={response.data.result[i].name}
                            />
                            <br />
                            {<b>{ru_role}</b>}

                        </Card>
                        // </Col>
                    )
                }
                this.setState({ Cards: cards, CardsC: response.data.count, loaded: true })
            });
    }

    componentDidMount() {
        this.fetch(1, 'https://promo.productlab.pro/api/users')
    }


    render() {
        return (
            this.state.loaded ? <div >

                <Row >
                    {this.state.Cards}
                </Row>
                <Pagination
                    onChange={(page) => { this.fetch(page, 'https://promo.productlab.pro/api/article') }}
                    pageSize={18}
                    total={this.state.CardsC}
                    style={{ float: "right" }}
                    showTotal={(total, range) => `Показаны ${range[0]}-${range[1]} из ${total} пользователей`}
                />
            </div > : null
        )
    }
}
export default Users;